import * as ActionTypes from './ActionTypes';

export const Promotions = (state = {
        isLoading: true,
        errMess: null,
        promotions: []
    }, action) => {
    switch(action.type) {
        case ActionTypes.ADD_PROMOS: 
            return {...state, isLoading: false, errMess: null, promotions: action.payload} //Spread Operator, Operador de Propagacion
        case ActionTypes.PROMOS_LOADING:
            return {...state, isLoading: true, errMess: null, promotions: []} //Spread Operator, Operador de Propagacion
        case ActionTypes.PROMOS_FAILED:
            return {...state, isLoading: false, errMess: action.payload, promotions: []} //Spread Operator, Operador de Propagacion
        default:
            return state
    }
}