import * as ActionTypes from './ActionTypes';

export const Dishes = (state = {
        isLoading: true,
        errMess: null,
        dishes: []
    }, action) => {
    switch(action.type) {
        case ActionTypes.ADD_DISHES: 
            return {...state, isLoading: false, errMess: null, dishes: action.payload} //Spread Operator, Operador de Propagacion
        case ActionTypes.DISHES_LOADING:
            return {...state, isLoading: true, errMess: null, dishes: []} //Spread Operator, Operador de Propagacion
        case ActionTypes.DISHES_FAIL:
            return {...state, isLoading: false, errMess: action.payload, dishes: []} //Spread Operator, Operador de Propagacion
        default:
            return state
    }
}