//import React, { Component } from 'react';
import Main from './components/MainComponent';
import './App.css';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux'; //Permite configurar mi react-redux app
import { ConfigureStore } from './redux/configureStore'; //Importamos la configuracion del bloque

const store = ConfigureStore()

function App() {
//class App extends Component {

  return (
    <Provider store={store} >
      <BrowserRouter>
        <div>
          <Main />
        </div>
      </BrowserRouter>
    </Provider>
  )

}

export default App;
